<template>
  <div class="classBox">
    <el-table
      :data="classList.filter(data => !search || data.className.toLowerCase().includes(search.toLowerCase()))"
      style="width: 100%"
      height="500"
      :header-cell-style="{fontWeight:600}"
    >
      <el-table-column label="序号" type="index" fixed width="60" align="center"></el-table-column>
      <el-table-column label="班级代号" prop="classNo" fixed width="120" align="center"></el-table-column>
      <el-table-column label="班级名称" prop="className" width="150" align="center"></el-table-column>
      <el-table-column label="专业代号" prop="professionKey" width="120" align="center"></el-table-column>
      <el-table-column label="专业名称" prop="professionName" width="180" align="center"></el-table-column>
      <el-table-column label="班级人数" prop="classNum" width="80" align="center"></el-table-column>
      <el-table-column label="就业人数" prop="employNum" width="80" align="center"></el-table-column>
      <el-table-column label="班长" prop="monitor" width="100" align="center"></el-table-column>
      <el-table-column label="班长联系方式" prop="monitorTel" width="150" align="center"></el-table-column>
      <el-table-column fixed="right" width="200" align="center">
        <template slot="header" slot-scope>
          <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
        </template>
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">修改</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <calssDialog
      :dialogFormVisible="dialogFormVisible"
      :dialogTitle="dialogTitle"
      :classForm="classInfo"
      @updateClass="updateClass"
      @changeDialogVisible="changeDialogVisible"
    ></calssDialog>
  </div>
</template>
<script>
import {
  deleteClass,
  interClassInfo
} from "../../../../../utils/employment.url";
import calssDialog from "./calssDialog.vue";
export default {
  components: { calssDialog },
  data() {
    return {
      userInfo: {},
      classList: [],
      search: "",
      dialogTitle: "修改班级信息",
      dialogFormVisible: false,
      classInfo: {}
    };
  },
  methods: {
    //获取班级列表信息
    getCounselorInfo() {
      interClassInfo({ counselorNo: this.userInfo.userId }, "get")
        .then(res => {
          if (res.status == 200) {
            this.classList = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    //修改班级信息
    updateClass() {
      this.getCounselorInfo();
    },
    handleEdit(index, row) {
      this.classInfo = row;
      // this.classForm.counselorNo = this.userInfo.userId;
      this.dialogFormVisible = true;
    },
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteClass(row, "delete").then(res => {
            if (res.status == 200) {
              this.getCounselorInfo();
              this.$message({
                message: res.message,
                type: "success"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    changeDialogVisible(value) {
      this.dialogFormVisible = value;
    }
  },
  created() {
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getCounselorInfo();
  }
};
</script>
<style lang="scss" scoped>
.classBox {
  width: 1000px;
  margin: 20px auto;

  .el-table {
    border-radius: 10px;
  }
}
</style>